import React from 'react';
import Btech from  '../Images/SidebarOptions/Btech.jpg';
import MBA   from  '../Images/SidebarOptions/Mba.jpg';
import MCA from  '../Images/SidebarOptions/Mca.jpg';
import MTech from  '../Images/SidebarOptions/Mtech.jpg';
import Moodle from  '../Images/SidebarOptions/Moodle.png';
import Library from  '../Images/SidebarOptions/Library.jpg';
import Hostel from  '../Images/SidebarOptions/Hostel.png';
import Add from '@material-ui/icons/Add';
import "../Css/SidebarOptions.css";



// src\Images\SidebarOptions\Btech.jpg
function SidebarOptions() {
    return (
    <div className = "sidebarOptions">
        <div className = "sidebarOption">
            <img src={Btech } alt="BTech"/>
            <p>BTech</p> 
        </div>

        <div className = "sidebarOption">
            <img src={MTech } alt="BTech"/>
            <p>MTech </p>
        </div>

        <div className = "sidebarOption">
            <img src={MBA } alt="BTech"/>
            <p>MBA</p> 
        </div>

        <div className = "sidebarOption">
            <img src={MCA } alt="BTech"/>
            <p>MCA</p>
        </div>

        <div className = "sidebarOption">
            <img src={Library } alt="BTech"/>
            <p>Library</p> 
        </div>

        <div className = "sidebarOption">
             <img src={Moodle} alt="BTech"/>
            <p>Moodle</p> 
        </div>    

        <div className = "sidebarOption">
            <img src={Hostel } alt="BTech"/>
            <p>Hostel</p> 
        </div>       
        
        <div className = "sidebarOption">
              <Add/>
              <p className = "text" >Discover </p>
        </div>
    </div>
    )
}
export default SidebarOptions  